l-table-column align=\"center\"\r\n                         label=\"服务工号\"\r\n                         prop=\"number\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"服务工号类型\"\r\n                         prop=\"user_status\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"地区\"\r\n                         prop=\"number\">\r\n          <template slot-scope=\"scope\">\r\n            <el-popover placement=\"top-start\"\r\n                        width=\"200\"\r\n                        trigger=\"hover\"\r\n                        :content=\"`${scope.row.province}${scope.row.city}${scope.row.county}`\">\r\n              <el-button slot=\"reference\"\r\n                         type=\"text\"\r\n                         class=\"ellipsis\">{{`${scope.row.province}${scope.row.city}${scope.row.county}`}}</el-button>\r\n            </el-popover>\r\n          </template>\r\n        </el-table-column>\r\n\r\n        <el-table-column align=\"center\"\r\n                         label=\"创建时间\"\r\n                         prop=\"create_time\">\r\n\r\n        </el-table-column>\r\n        <el-table-column align=\"center\"\r\n                         label=\"操作\">\r\n          <template slot-scope=\"scope\">\r\n            <el-switch v-model=\"scope.row.job_status\"\r\n                       active-color=\"#13ce66\"\r\n                       :active-value='1'\r\n                       :inactive-value='2'\r\n                       inactive-color=\"#ff4949\"\r\n                       @change=\"(value)=>changeOpen(value,scope.row)\">\r\n            </el-switch>\r\n            <el-button type=\"primary\"\r\n                       size=\"small\"\r\n                       style=\"margin-left:10px\"\r\n                       @click=\"serviceForever(scope.row,scope.$index)\"\r\n                       :loading='Loading[scope.$index]'\r\n                       :disabled='scope.row.forever_status==1'>长期有效</el-button>\r\n          </template>\r\n        </el-table-column>\r\n\r\n      </el-table>\r\n\r\n      <div class=\"page_center\"\r\n           v-show=\"pageCount&&pageCount>1\">\r\n        <paging :page-count=\"pageCount\"\r\n                :page=\"page\"\r\n                @index=\"paging\"></paging>\r\n      </div>\r\n    </div>\r\n\r\n  </div>\r\n</template>\r\n<script type=\"text/ecmascript-6\">\r\n//  import page from '../common.js';\r\nimport Viewer from '@/utils/Viewer'\r\nexport default {\r\n  data () {\r\n    return {\r\n      authList: [],\r\n      threeAuthList: [],\r\n      token: window.sessionStorage.getItem('bbytoken'),\r\n      seCurId: 0,\r\n      page: 0,\r\n      pageCount: 0,\r\n      list: [],\r\n      detailsVisible: false,\r\n      detailsList: {},\r\n      Loading: [],\r\n      open: [], //开关\r\n    }\r\n  },\r\n  methods: {\r\n    paging (e) {\r\n      this.page = e;\r\n      this.init()\r\n    },\r\n    async init () {\r\n      try {\r\n        const res = await this.$axios.post('admin/UserServiceCode/index', { token: this.token, page: this.page })\r\n        this.list = res.data.data.list || []\r\n        this.pageCount = res.data.data.rows || 0;\r\n      } catch (err) {\r\n        throw (err)\r\n      }\r\n    },\r\n    changeOpen (val, item) { //开关\r\n      let reasonText = val == 1 ? '开启理由' : '关闭理由'\r\n      this.$prompt(`请输入${reasonText}`, '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        inputPattern: /\\S/,\r\n        inputErrorMessage: `${reasonText}不能为空`\r\n      }).then(async ({ value }) => {\r\n        try {\r\n          const res = await this.$axios.post('admin/UserServiceCode/serviceAudit', { token: this.token, uid: item.uid, id: item.id, job_status: val, reason: value })\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: 'success' })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (error) {\r\n          throw (error)\r\n        }\r\n      }).catch(() => {\r\n        item.job_status = item.job_status == 1 ? 2 : 1\r\n      });\r\n    },\r\n    serviceForever (item, index) {   //服务工号长期有效\r\n      this.$confirm('此操将开启长期有效, 是否继续?', '提示', {\r\n        confirmButtonText: '确定',\r\n        cancelButtonText: '取消',\r\n        type: 'warning'\r\n      }).then(async () => {\r\n        try {\r\n          this.Loading[index] = true\r\n          const res = await this.$axios.post('admin/UserServiceCode/serviceForever', { token: this.token, uid: item.uid, id: item.id, forever_status: 1 })\r\n          this.Loading[index] = false\r\n          if (res.data.code == 1) {\r\n            this.$message({ message: res.data.msg, type: \"success\" })\r\n            this.init()\r\n          } else {\r\n            this.$message.error(res.data.msg)\r\n          }\r\n        } catch (error) {\r\n          this.Loading[index] = false\r\n          throw (error)\r\n        }\r\n      }).catch(() => { });\r\n    },\r\n    erAuth () {\r\n      var id = this.$route.query.id;\r\n      this.curId = id;\r\n      this.$axios.post('admin/Auth/erAuth', {\r\n        token: this.token,\r\n        id: id\r\n      })\r\n        .then(res => {\r\n\r\n          if (res.data.code == 1) {\r\n            var arr = res.data.data;\r\n            for (var i = 0; i < arr.length; i++) {\r\n              if (arr[i].name == '服务工号') {\r\n                this.seCurId = arr[i].id;\r\n              }\r\n            }\r\n            this.authList = arr;\r\n          } else {\r\n            this.$alert(res.data.msg, '提示', { type: 'error' });\r\n          }\r\n        })\r\n        .catch(err => {\r\n          alert(err);\r\n        })\r\n    }\r\n  },\r\n\r\n  mounted () {\r\n    this.init()\r\n    this.erAuth()\r\n  },\r\n\r\n}\r\n</script>\r\n<style scoped>\r\n.ellipsis {\r\n  width: 100px;\r\n  overflow: hidden;\r\n  text-overflo